'use client';

import Link from 'next/link';
import { useEffect, useState } from 'react';
import { motion } from 'framer-motion';
import {
    Search,
    Map as MapIcon,
    TrendingUp,
    Activity,
    ArrowRight,
    Zap,
    Clock,
    Target,
    Plus
} from 'lucide-react';
import { Card, Button, Badge, StatCard, Skeleton } from '@/components/ui';

interface Scan {
    id: string;
    keyword: string;
    status: string;
    gridSize: number;
    radius: number;
    frequency: string;
    createdAt: string;
    centerLat: number;
    centerLng: number;
}

interface DashboardData {
    scansCount: number;
    recentScans: Scan[];
}

export default function DashboardPage() {
    const [data, setData] = useState<DashboardData | null>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetch('/api/dashboard')
            .then(res => res.json())
            .then(setData)
            .catch(console.error)
            .finally(() => setLoading(false));
    }, []);

    return (
        <div className="p-8 max-w-7xl mx-auto">
            {/* Header */}
            <motion.header
                initial={{ opacity: 0, y: -20 }}
                animate={{ opacity: 1, y: 0 }}
                className="flex flex-col md:flex-row justify-between items-start md:items-end gap-6 mb-10"
            >
                <div>
                    <div className="flex items-center gap-3 mb-2">
                        <span className="text-sm text-zinc-500">Welcome back</span>
                        <Badge variant="purple" dot>Pro</Badge>
                    </div>
                    <h1 className="text-4xl font-bold text-white">Dashboard</h1>
                    <p className="text-zinc-400 mt-2">Monitor your local SEO grid tracking performance</p>
                </div>
                <Link href="/scans/new">
                    <Button size="lg" className="shadow-xl">
                        <Plus className="w-5 h-5" />
                        New Tracker
                    </Button>
                </Link>
            </motion.header>

            {/* Stats Grid */}
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.1 }}
                className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-4 gap-6 mb-10"
            >
                {loading ? (
                    <>
                        <Skeleton className="h-32" />
                        <Skeleton className="h-32" />
                        <Skeleton className="h-32" />
                        <Skeleton className="h-32" />
                    </>
                ) : (
                    <>
                        <StatCard
                            title="Total Scans"
                            value={data?.scansCount || 0}
                            icon={Activity}
                            change="+12%"
                            trend="up"
                        />
                        <StatCard
                            title="Grid Points Analyzed"
                            value="156"
                            icon={MapIcon}
                            change="+8%"
                            trend="up"
                        />
                        <StatCard
                            title="Avg. Visibility"
                            value="Top 5"
                            icon={TrendingUp}
                            change="+3 spots"
                            trend="up"
                        />
                        <StatCard
                            title="Active Trackers"
                            value="2"
                            icon={Target}
                        />
                    </>
                )}
            </motion.div>

            <div className="grid grid-cols-1 xl:grid-cols-3 gap-8">
                {/* Recent Scans */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.2 }}
                    className="xl:col-span-2"
                >
                    <div className="flex items-center justify-between mb-6">
                        <h2 className="text-xl font-bold text-white">Recent Scans</h2>
                        <Link href="/scans" className="text-sm text-zinc-400 hover:text-white transition-colors flex items-center gap-1">
                            View all <ArrowRight className="w-4 h-4" />
                        </Link>
                    </div>

                    <div className="space-y-4">
                        {loading ? (
                            <>
                                <Skeleton className="h-20" />
                                <Skeleton className="h-20" />
                                <Skeleton className="h-20" />
                            </>
                        ) : data?.recentScans.length === 0 ? (
                            <Card className="text-center py-16">
                                <div className="w-16 h-16 mx-auto mb-4 rounded-2xl bg-white/5 flex items-center justify-center">
                                    <MapIcon className="w-8 h-8 text-zinc-500" />
                                </div>
                                <h3 className="text-lg font-semibold text-white mb-2">No scans yet</h3>
                                <p className="text-zinc-400 mb-6">Start tracking your local rankings with a grid scan</p>
                                <Link href="/scans/new">
                                    <Button>Create Your First Scan</Button>
                                </Link>
                            </Card>
                        ) : (
                            data?.recentScans.map((scan, i) => (
                                <motion.div
                                    key={scan.id}
                                    initial={{ opacity: 0, x: -20 }}
                                    animate={{ opacity: 1, x: 0 }}
                                    transition={{ delay: 0.1 * i }}
                                >
                                    <Link href={`/scans/${scan.id}`}>
                                        <Card hover className="flex items-center gap-4 cursor-pointer group">
                                            <div className="w-12 h-12 rounded-xl bg-gradient-to-br from-blue-500/20 to-purple-500/20 flex items-center justify-center border border-white/10 group-hover:border-white/20 transition-colors">
                                                <Search className="w-5 h-5 text-blue-400" />
                                            </div>
                                            <div className="flex-1">
                                                <div className="flex items-center gap-3">
                                                    <h3 className="font-semibold text-white group-hover:text-blue-400 transition-colors">
                                                        {scan.keyword}
                                                    </h3>
                                                    <Badge
                                                        variant={
                                                            scan.status === 'COMPLETED' ? 'success' :
                                                                scan.status === 'RUNNING' ? 'primary' :
                                                                    'default'
                                                        }
                                                        dot={scan.status === 'RUNNING'}
                                                    >
                                                        {scan.status}
                                                    </Badge>
                                                </div>
                                                <div className="flex items-center gap-4 text-sm text-zinc-500 mt-1">
                                                    <span className="flex items-center gap-1">
                                                        <Clock className="w-3 h-3" />
                                                        {new Date(scan.createdAt).toLocaleDateString()}
                                                    </span>
                                                    <span>{scan.gridSize}x{scan.gridSize} Grid</span>
                                                    <span>{scan.radius}km</span>
                                                </div>
                                            </div>
                                            <ArrowRight className="w-5 h-5 text-zinc-600 group-hover:text-white group-hover:translate-x-1 transition-all" />
                                        </Card>
                                    </Link>
                                </motion.div>
                            ))
                        )}
                    </div>
                </motion.div>

                {/* Quick Actions Panel */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.3 }}
                    className="space-y-6"
                >
                    <h2 className="text-xl font-bold text-white">Quick Actions</h2>

                    {/* Featured Action */}
                    <div className="gradient-border">
                        <div className="bg-gradient-to-br from-blue-900/30 to-purple-900/30 rounded-2xl p-6">
                            <div className="w-12 h-12 rounded-xl bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center mb-4 shadow-lg glow-blue">
                                <Zap className="w-6 h-6 text-white" />
                            </div>
                            <h3 className="text-lg font-bold text-white mb-2">Smart Grid Scan</h3>
                            <p className="text-sm text-zinc-400 mb-4">
                                Automatically analyze competitor density and recommend optimal grid configurations.
                            </p>
                            <Link href="/scans/new">
                                <Button variant="secondary" className="w-full">
                                    Start Smart Scan
                                </Button>
                            </Link>
                        </div>
                    </div>

                    {/* Tips */}
                    <Card>
                        <h3 className="font-semibold text-white mb-4 flex items-center gap-2">
                            <span className="text-blue-400">ðŸ’¡</span> Pro Tip
                        </h3>
                        <p className="text-sm text-zinc-400">
                            Schedule weekly scans to track ranking changes over time.
                            Historical data helps identify patterns and optimize your local SEO strategy.
                        </p>
                    </Card>

                    {/* Stats Preview */}
                    <Card>
                        <h3 className="font-semibold text-white mb-4">This Week</h3>
                        <div className="space-y-4">
                            <div className="flex items-center justify-between">
                                <span className="text-sm text-zinc-400">Scans Completed</span>
                                <span className="text-lg font-bold text-white">12</span>
                            </div>
                            <div className="flex items-center justify-between">
                                <span className="text-sm text-zinc-400">Points Analyzed</span>
                                <span className="text-lg font-bold text-white">324</span>
                            </div>
                            <div className="flex items-center justify-between">
                                <span className="text-sm text-zinc-400">Top 3 Visibility</span>
                                <span className="text-lg font-bold text-emerald-400">68%</span>
                            </div>
                        </div>
                    </Card>
                </motion.div>
            </div>
        </div>
    );
}
